<template>
  <view class="profile-container">
    <view class="profile-header">
      <button 
        v-if="showLoginBtn" 
        open-type="chooseAvatar" 
        @chooseavatar="onChooseAvatar"
        class="avatar-wrapper"
      >
        <image class="profile-avatar" :src="userInfo.avatar || 'https://randomuser.me/api/portraits/men/32.jpg'" />
      </button>
      <image v-else class="profile-avatar" :src="userInfo.avatar || 'https://randomuser.me/api/portraits/men/32.jpg'" />
      
      <view v-if="showLoginBtn" class="nickname-input">
        <input 
          type="nickname" 
          placeholder="ËØ∑ËæìÂÖ•ÊòµÁß∞" 
          @blur="onNicknameInput" 
          class="nickname-field" 
        />
      </view>
      <text v-else class="profile-nick">HiÔºå{{ userInfo.nickname || 'ÈÖíÂèã' }}</text>
      
      <button
        v-if="showLoginBtn && tempUserInfo.avatar && tempUserInfo.nickname"
        @tap="doWechatLogin"
        type="primary"
        class="main-login-btn ins-btn"
      >ÁÇπÂáªÁôªÂΩï</button>
      <view v-if="showLoginBtn && (!tempUserInfo.avatar || !tempUserInfo.nickname)" class="login-tip">
        ËØ∑ÂÖàÈÄâÊã©Â§¥ÂÉèÂπ∂ËæìÂÖ•ÊòµÁß∞
      </view>
      <button
        v-if="!showLoginBtn"
        @tap="logout"
        type="default"
        class="logout-btn"
      >ÈÄÄÂá∫ÁôªÂΩï</button>
    </view>
    <view class="profile-list">
      <view class="profile-item" @tap="goOrder">
        <text class="profile-icon">üì¶</text>
        <text class="profile-label">ÊàëÁöÑËÆ¢Âçï</text>
      </view>
    </view>
    
    <!-- ÊéàÊùÉÂ§±Ë¥•ÊèêÁ§∫ -->
    <button v-if="showSettingBtn" @tap="openSetting" type="warn" class="ins-btn ins-warn-btn">ÂéªËÆæÁΩÆÈáçÊñ∞ÊéàÊùÉ</button>
    <view v-if="showSettingBtn" class="ins-warn-text">ËØ∑Âú®ËÆæÁΩÆ‰∏≠ÊéàÊùÉÂêéÔºåÂÜçÁÇπÂáª"ÂæÆ‰ø°‰∏ÄÈîÆÁôªÂΩï"</view>
  </view>
</template>

<script>
import api from '@/utils/api.js';

export default {
  data() {
    return {
      userInfo: uni.getStorageSync('userInfo') || {},
      showSettingBtn: false,
      showLoginBtn: !uni.getStorageSync('token'),
      tempUserInfo: {
        avatar: '',
        nickname: ''
      }
    };
  },
  onLoad() {
    this.initUserStatus();
  },
  onShow() {
    // ‰ºòÂÖàÁî®Êú¨Âú∞userInfoÊ∏≤Êüì
    const userInfo = uni.getStorageSync('userInfo') || {};
    this.userInfo = userInfo;
    this.showLoginBtn = !uni.getStorageSync('token');
    
    // Â¶ÇÊûúÊú™ÁôªÂΩïÔºåÊòæÁ§∫ÊèêÁ§∫‰ø°ÊÅØ
    if (this.showLoginBtn) {
      uni.showToast({
        title: 'ËØ∑ÈÄâÊã©Â§¥ÂÉèÂíåÊòµÁß∞ÂêéÁôªÂΩï',
        icon: 'none',
        duration: 2000
      });
    }
  },
  methods: {
    initUserStatus() {
      const token = uni.getStorageSync('token');
      if (!token) {
        this.showLoginBtn = true;
        this.showSettingBtn = false;
      } else {
        this.showLoginBtn = false;
        this.showSettingBtn = false;
      }
    },
    goOrder() {
      uni.switchTab({ url: '/pages/order/index' });
    },
    
    // Ëé∑ÂèñÂ§¥ÂÉè
    onChooseAvatar(e) {
      const { avatarUrl } = e.detail;
      this.tempUserInfo.avatar = avatarUrl;
      
      // Â∞Ü‰∏¥Êó∂Â§¥ÂÉèÊòæÁ§∫Âà∞ÁïåÈù¢
      this.userInfo.avatar = avatarUrl;
      
      // ÊèêÁ§∫Áî®Êà∑
      if (this.tempUserInfo.nickname) {
        uni.showToast({
          title: 'Â§¥ÂÉèÂ∑≤ÈÄâÊã©ÔºåËØ∑ÁÇπÂáªÁôªÂΩï',
          icon: 'none',
          duration: 1500
        });
      } else {
        uni.showToast({
          title: 'ËØ∑ËæìÂÖ•ÊòµÁß∞',
          icon: 'none',
          duration: 1500
        });
      }
    },
    
    // Ëé∑ÂèñÊòµÁß∞
    onNicknameInput(e) {
      const nickname = e.detail.value;
      this.tempUserInfo.nickname = nickname;
      
      // Â∞Ü‰∏¥Êó∂ÊòµÁß∞ÊòæÁ§∫Âà∞ÁïåÈù¢
      this.userInfo.nickname = nickname;
      
      // ÊèêÁ§∫Áî®Êà∑
      if (this.tempUserInfo.avatar) {
        uni.showToast({
          title: 'ÊòµÁß∞Â∑≤ËæìÂÖ•ÔºåËØ∑ÁÇπÂáªÁôªÂΩï',
          icon: 'none',
          duration: 1500
        });
      } else {
        uni.showToast({
          title: 'ËØ∑ÈÄâÊã©Â§¥ÂÉè',
          icon: 'none',
          duration: 1500
        });
      }
    },
    
    // Á°ÆËÆ§ÁôªÂΩï
    doWechatLogin() {
      // Ê£ÄÊü•ÊòØÂê¶Â∑≤ÈÄâÊã©Â§¥ÂÉèÂíåÂ°´ÂÜôÊòµÁß∞
      if (!this.tempUserInfo.avatar) {
        uni.showToast({
          title: 'ËØ∑ÈÄâÊã©Â§¥ÂÉè',
          icon: 'none'
        });
        return;
      }
      
      if (!this.tempUserInfo.nickname) {
        uni.showToast({
          title: 'ËØ∑ËæìÂÖ•ÊòµÁß∞',
          icon: 'none'
        });
        return;
      }
      
      // ÊòæÁ§∫Âä†ËΩΩ‰∏≠
      uni.showLoading({
        title: 'ÁôªÂΩï‰∏≠...',
        mask: true
      });
      
      // 1. Ë∞ÉÁî® wx.login() Ëé∑Âèñ‰∏¥Êó∂ÁôªÂΩïÂá≠ËØÅ code
      uni.login({
        success: (res) => {
          if (res.code) {
            console.log('Ëé∑ÂèñÂà∞‰∏¥Êó∂ÁôªÂΩïÂá≠ËØÅcode:', res.code);
            
            // 2. Â∞ÜcodeÂèëÈÄÅÂà∞ÂºÄÂèëËÄÖÊúçÂä°Âô®
            api.login(res.code)
              .then(resp => {
                // ÈöêËóèÂä†ËΩΩ
                uni.hideLoading();
                console.log('ÁôªÂΩïÊàêÂäüÔºåÂìçÂ∫î:', JSON.stringify(resp));
                
                if (!resp.data.token) {
                  throw new Error('ÊúçÂä°Âô®Êú™ËøîÂõûÊúâÊïàÁöÑÁôªÂΩïÂá≠ËØÅ');
                }
                
                // 3. ‰øùÂ≠òËá™ÂÆö‰πâÁôªÂΩïÊÄÅ
                uni.setStorageSync('token', resp.data.token);
                
                // 4. ‰øùÂ≠òÁî®Êà∑‰ø°ÊÅØ
                const userInfo = {
                  ...resp.data.userInfo,
                  nickname: this.tempUserInfo.nickname,
                  avatar: this.tempUserInfo.avatar
                };
                uni.setStorageSync('userInfo', userInfo);
                this.userInfo = userInfo;
                this.showLoginBtn = false;
                
                // 5. Êõ¥Êñ∞Áî®Êà∑‰ø°ÊÅØÂà∞ÊúçÂä°Âô®
                this.updateUserInfo(resp.data.token, this.tempUserInfo.nickname, this.tempUserInfo.avatar);
                
                uni.showToast({
                  title: 'ÁôªÂΩïÊàêÂäü',
                  icon: 'success'
                });
              })
              .catch(err => {
                // ÈöêËóèÂä†ËΩΩ
                uni.hideLoading();
                
                console.error('ÁôªÂΩïÂ§±Ë¥•', err);
                // ÊòæÁ§∫Êõ¥ËØ¶ÁªÜÁöÑÈîôËØØ‰ø°ÊÅØ
                uni.showModal({
                  title: 'ÁôªÂΩïÂ§±Ë¥•',
                  content: `ÊúçÂä°Âô®ËøîÂõûÈîôËØØ: ${err.message || 'Êú™Áü•ÈîôËØØ'}`,
                  showCancel: false
                });
              });
          } else {
            // ÈöêËóèÂä†ËΩΩ
            uni.hideLoading();
            
            console.error('Ëé∑ÂèñcodeÂ§±Ë¥•', res);
            uni.showToast({ 
              title: 'Ëé∑Âèñ‰∏¥Êó∂ÁôªÂΩïÂá≠ËØÅÂ§±Ë¥•', 
              icon: 'none' 
            });
          }
        },
        fail: (err) => {
          // ÈöêËóèÂä†ËΩΩ
          uni.hideLoading();
          
          console.error('wx.login Â§±Ë¥•', err);
          uni.showToast({ 
            title: 'ÂæÆ‰ø°ÁôªÂΩïÂ§±Ë¥•', 
            icon: 'none' 
          });
        }
      });
    },
    
    // Êõ¥Êñ∞Áî®Êà∑‰ø°ÊÅØÂà∞ÊúçÂä°Âô®
    updateUserInfo(token, nickname, avatar) {
      api.updateUserInfo(nickname, avatar)
        .then(resp => {
          console.log('Áî®Êà∑‰ø°ÊÅØÊõ¥Êñ∞ÊàêÂäü', resp);
        })
        .catch(err => {
          console.error('Áî®Êà∑‰ø°ÊÅØÊõ¥Êñ∞Â§±Ë¥•', err);
          // ‰∏çÂΩ±ÂìçÁî®Êà∑‰ΩìÈ™åÔºåÈùôÈªòÂ§±Ë¥•
        });
    },
    
    // ‰∏ä‰º†Â§¥ÂÉèÂà∞ÊúçÂä°Âô®
    uploadAvatar(tempFilePath, callback) {
      // ÂæÆ‰ø°‰∏¥Êó∂Êñá‰ª∂Ë∑ØÂæÑÂèØËÉΩÂú®Êüê‰∫õÊÉÖÂÜµ‰∏ãÊó†Ê≥ïÁõ¥Êé•‰ΩøÁî®
      // Ê£ÄÊü•ÊòØÂê¶ÊòØ‰∏¥Êó∂Êñá‰ª∂Ë∑ØÂæÑÔºåÂ¶ÇÊûúÊòØÂàôÈúÄË¶ÅÂ§ÑÁêÜ
      if (tempFilePath.startsWith('wxfile://') || tempFilePath.startsWith('http://tmp/')) {
        // Â∞Ü‰∏¥Êó∂Êñá‰ª∂‰∏ä‰º†Âà∞ÊúçÂä°Âô®ÊàñËΩ¨‰∏∫base64
        uni.getFileSystemManager().readFile({
          filePath: tempFilePath,
          encoding: 'base64',
          success: (res) => {
            // Â∞Übase64ÁºñÁ†ÅÁöÑÂõæÁâá‰º†ÈÄíÁªôÂõûË∞É
            const base64Img = 'data:image/jpeg;base64,' + res.data;
            callback(base64Img);
          },
          fail: (err) => {
            console.error('ËØªÂèñÂ§¥ÂÉèÊñá‰ª∂Â§±Ë¥•', err);
            // ËØªÂèñÂ§±Ë¥•Êó∂ÔºåÂ∞ùËØïÁõ¥Êé•‰ΩøÁî®‰∏¥Êó∂Ë∑ØÂæÑ
            callback(tempFilePath);
          }
        });
      } else {
        // Â¶ÇÊûú‰∏çÊòØ‰∏¥Êó∂Êñá‰ª∂Ë∑ØÂæÑÔºåÁõ¥Êé•‰ΩøÁî®
        callback(tempFilePath);
      }
    },
    
    openSetting() {
      uni.openSetting({
        success: (res) => {
          this.showSettingBtn = false;
          uni.showToast({ title: 'ËØ∑ÈáçÊñ∞ÁôªÂΩï', icon: 'none' });
        }
      });
    },
    
    // ÈÄÄÂá∫ÁôªÂΩï
    logout() {
      uni.showModal({
        title: 'ÊèêÁ§∫',
        content: 'Á°ÆÂÆöË¶ÅÈÄÄÂá∫ÁôªÂΩïÂêóÔºü',
        success: (res) => {
          if (res.confirm) {
            // Ê∏ÖÈô§tokenÂíåÁî®Êà∑‰ø°ÊÅØ
            uni.removeStorageSync('token');
            uni.removeStorageSync('userInfo');
            
            // Êõ¥Êñ∞Áä∂ÊÄÅ
            this.userInfo = {};
            this.showLoginBtn = true;
            this.tempUserInfo = {
              avatar: '',
              nickname: ''
            };
            
            uni.showToast({
              title: 'Â∑≤ÈÄÄÂá∫ÁôªÂΩï',
              icon: 'success'
            });
          }
        }
      });
    }
  }
};
</script>

<style scoped>
