}
.profile-label {
  font-size: 32rpx;
  color: #333;
}
.ins-btn {
  background: linear-gradient(90deg, #f7cac9 0%, #92a8d1 100%);
  border: none;
  color: #fff;
  font-size: 32rpx;
  padding: 16rpx 60rpx;
  border-radius: 40rpx;
  box-shadow: 0 8rpx 16rpx rgba(0,0,0,0.1);
}
.ins-warn-btn {
  margin: 30rpx auto;
  display: block;
